#toast-container > div {
  opacity: 0.9;
  -ms-filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=90);
  filter: alpha(opacity=90);
}

.article-list {
  position: relative;
  padding-left: 0;
}

.article-list .article-list-item {
  position: relative;
  padding-top: 30px;
  padding-bottom: 15px;
  list-style: none;
  border-bottom: 1px solid #eee;
}

.article-list .article-list-name {
  font-weight: 400;
  margin: 0 0 8px;
  font-size: 20px;
  line-height: 1.2;
}

.article-list .article-list-name:hover {
  text-decoration: none;
}

.article-list .article-list-description {
  max-width: 550px;
  margin-top: 8px;
  margin-bottom: 0;
  font-size: 14px;
  color: #888;
}

.article-list .article-list-meta {
  display: block;
  margin-top: 8px;
  margin-bottom: 0;
  font-size: 13px;
  color: #888;
}

.article-list .article-list-meta span {
  margin-right: 0.6em;
}

.has-warning .input-group-addon, .has-error .input-group-addon, .has-success .input-group-addon {
  color: #666;
  border-color: transparent;
  background-color: transparent;
}

.form-group-lg select,
.form-group-lg select.form-control {
  line-height: 1.5;
}

.share {
  margin: 20px 0;
}

.mobile-visible {
  display: none;
}

.jumbotron {
  padding-top: 28px;
  padding-bottom: 28px;
  background-color: #2196f3;
}

.jumbotron .jumbotron-title, .jumbotron .jumbotron-desc, .jumbotron .jumbotron-meta {
  color: #ffffff;
}

.jumbotron .jumbotron-title {
  font-size: 2em;
}

.jumbotron .jumbotron-desc {
  font-size: 1.2em;
}

.jumbotron .jumbotron-meta {
  font-size: 1em;
}

.jumbotron .jumbotron-meta a {
  color: #ffffff;
}

.jumbotron .jumbotron-meta a:hover {
  color: #f4645f;
}

.home-jumbotron .jumbotron-title {
  font-size: 2.8em;
}

@media (max-width: 50em) {
  .mobile-block {
    display: block;
    float: none;
  }
  .mobile-hidden {
    display: none;
  }
  .mobile-visible {
    display: inline-block;
  }
  .markdown-body h1 {
    font-size: 2em !important;
  }
  .markdown-body h2 {
    font-size: 1.5em !important;
  }
  .markdown-body h3 {
    font-size: 1.3em !important;
  }
  .markdown-body h4 {
    font-size: 1.2em !important;
  }
  .markdown-body h5 {
